body {
    font-family: Arial, Helvetica, sans-serif;
}

h1 {
    font-size: 2em;
}

form {
    display: flex;
    flex-direction: column;

    margin-bottom: 50px;
}

label {
    display: flex;
    flex-direction: column;
}

label>input {
    margin-top: 10px;
}

input {
    width: 200px;
    font-size: 20px;
}

input[type='submit'] {
    width: 80px;
    height: 29px;

    margin-top: 10px;
}

.error {
    margin: 0;
    margin-top: 5px;
    
    font-size: 12px;
    color: brown;
}

.tip {
    margin: 0;

    font-size: 12px;
    color: grey;
}

.mentor {
    font-size: 1.3em;
    margin-bottom: 30px;
}

/* Add this attribute to the element that needs a tooltip */
[data-tooltip] {
    position: relative;
    z-index: 2;
    cursor: pointer;
}

/* Hide the tooltip content by default */
[data-tooltip]:before,
[data-tooltip]:after {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
}

/*Position tooltip above the element */
[data-tooltip]:before {
    position: absolute;
    bottom: 30%;

    margin-bottom: 5px;
    margin-left: 18px;
    padding: 7px;

    width: 50px;
    border-radius: 3px;
    background-color: #000;
    background-color: hsla(0, 0%, 20%, 0.9);
    color: #fff;
    content: attr(data-tooltip);
    text-align: center;
    font-size: 14px;
    line-height: 1.2;
} 

/* Show tooltip content on hover */
[data-tooltip]:hover:before,
[data-tooltip]:hover:after {
    visibility: visible;
    opacity: 1;
}

th {
    font-size: 12px;
}

th, td {
    width: 70px;
}

.statuses {
    margin-bottom: 50px;
}

.statuses th, td {
    width: auto;
}

.statuses th {
    font-size: 14px;
}


.statuses td:first-child {
    display: inline-block;
    margin-right: 15px;

    color: rgb(73, 73, 73);
}

.transparent-text {
    display: block;

    width: 100%;

    color: transparent;
}

.transparent-text:hover,
.transparent-text:active,
.transparent-text:visited,
.transparent-text:focus {
    color: transparent;
}

/* Tasks statuses */

.ok {
    background-color: green;
}

.studentFail {
    background-color: red;
}

.mentorFail {
    background-color: brown;
}

.waitingForChecking {
    background-color: pink;
}

.doneInAdvance {
    background-color: lightgreen;
}

.inProgress {
    background-color: yellow;
}

.todo {
    background-color:grey;
}
